/* You can add global styles to this file, and also import other style files */

/* override bootstrap styles */
@import url("http://fonts.googleapis.com/css?family=Oswald:300,400:latin,latin-ext");

.navbar-form input,
.form-inline input {
  width: auto;
}

body {
  padding-top: 50px;
  color: #646464;
  background-color: #efefef;
}

h1,
h2,
h3,
.highlight,
.navbar a,
#masthead h4 {
  font-family: "Oswald", arial narrow, sans-serif;
}

footer {
  margin-top: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #cecece;
}

#masthead {
  min-height: 199px;
  background-color: #000044;
  color: #aaaacc;
}

#masthead h1 {
  font-size: 55px;
  line-height: 1;
  color: #fefeff;
  margin-top: 50px;
}

#masthead .well {
  margin-top: 31px;
  background-color: #111166;
  border-color: #000022;
  color: #efefff;
  min-height: 127px;
}

#topPanel {
  padding-top: 0px;
  padding-left: 22px;
  padding-right: 22px;
}
#topPanel .panel-body {
  padding-top: 0px;
}

.navbar.affix {
  position: fixed;
  top: 0;
  width: 100%;
}

.top-spacer {
  background-color: #ffffff;
  height: 40px;
}

a,
a:hover {
  color: #223344;
  text-decoration: none;
}

.icon-bar {
  background-color: #fff;
}

@media screen and (min-width: 768px) {
  #masthead h1 {
    font-size: 80px;
  }
}

.navbar-bright {
  background-color: #111166;
  color: #fff;
  margin-bottom: 0;
  border-width: 0;
}

.navbar-bright a,
.navbar-bright .navbar-brand,
#masthead h4 {
  color: #ccccdd;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.08em;
}

.navbar-bright li > a:hover {
  background-color: #000044;
}

.dropdown-menu {
  min-width: 250px;
}

.caret {
  color: #fff;
}

.navbar-toggle {
  color: #fff;
  border-width: 0;
}

.navbar-toggle:hover {
  background-color: #fff;
}

.panel {
  border-color: transparent;
  border-radius: 0;
}

.thumbnail {
  margin-bottom: 8px;
}

.img-container {
  overflow: hidden;
  height: 170px;
}

.img-container img {
  min-width: 280px;
  min-height: 180px;
  max-width: 380px;
  max-height: 280px;
}

.txt-container {
  overflow: hidden;
  height: 100px;
}

.panel .lead {
  overflow: hidden;
  height: 90px;
}

.label-float {
  margin: 0 auto;
  position: absolute;
  top: 0;
  z-index: 1;
  width: 100%;
  opacity: 0.9;
  padding: 6px;
  color: #fff;
}
.centre {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.btn-twitter {
  background-color: #4099ff;
  color: #fff;
}
.btn-facebook {
  background-color: #3b5998;
  color: #fff;
}
.btn-google-plus {
  background-color: #d34836;
  color: #fff;
}

.btn-twitter:hover {
  background-color: #3088ff;
  color: #62bbff;
}
.btn-facebook:hover {
  background-color: #2a4998;
  color: #4c6bba;
}
.btn-google-plus:hover {
  background-color: #c23836;
  color: #e45a58;
}
